import { CommonModule } from '@angular/common';
import { Component, ElementRef, forwardRef, Inject, Injector, NgModule, Optional, ViewEncapsulation } from '@angular/core';
import { MAT_FORM_FIELD_DEFAULT_OPTIONS } from '@angular/material';

import { OSharedModule } from '../../../shared';
import { OFormComponent } from '../../form/form-components';
import { DEFAULT_INPUTS_O_CONTAINER, OContainerComponent } from '../o-container-component.class';

export const DEFAULT_INPUTS_O_ROW = [
  ...DEFAULT_INPUTS_O_CONTAINER
];

@Component({
  moduleId: module.id,
  selector: 'o-row',
  template: `
    <div #container fxLayout="column" class="o-container" fxFill>
      <div #containerTitle *ngIf="hasHeader()" fxLayoutAlign="start center" class="o-container-title" layout-padding>
        <mat-icon *ngIf="icon">{{ icon }}</mat-icon>
        <span *ngIf="title">{{ title | oTranslate }}</span>
      </div>
      <div [class.o-container-gap]="hasHeader() || (elevation > 0 && elevation <= 12)" class="o-container-scroll o-scroll">
        <div class="o-container-outline" *ngIf="isAppearanceOutline() && hasHeader()">
          <div class="o-container-outline-start"></div>
          <div class="o-container-outline-gap"></div>
          <div class="o-container-outline-end"></div>
        </div>
        <div fxLayout="row" fxLayoutAlign="{{ layoutAlign }}" fxLayoutGap="{{ layoutGap }}" fxFlex="grow">
          <ng-content></ng-content>
        </div>
      </div>
    </div>
  `,
  styles: [`
    .o-row>.o-container{width:100%}.o-row>.o-container .o-container-scroll{overflow:auto;position:relative}
  `],
  inputs: DEFAULT_INPUTS_O_ROW,
  encapsulation: ViewEncapsulation.None,
  host: {
    '[class.o-row]': 'true',
    '[class.o-appearance-outline]': 'isAppearanceOutline()',
    '[class.o-appearance-outline-title]': 'hasTitleInAppearanceOutline()'
  }
})
export class ORowComponent extends OContainerComponent {

  public static DEFAULT_INPUTS_O_ROW = DEFAULT_INPUTS_O_ROW;

  constructor(
    @Optional() @Inject(forwardRef(() => OFormComponent)) protected form: OFormComponent,
    protected elRef: ElementRef,
    protected injector: Injector,
    @Optional() @Inject(MAT_FORM_FIELD_DEFAULT_OPTIONS) protected matFormDefaultOption
  ) {
    super(form, elRef, injector, matFormDefaultOption);
  }

}

@NgModule({
  declarations: [ORowComponent],
  imports: [CommonModule, OSharedModule],
  exports: [ORowComponent]
})
export class ORowModule { }
